import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

export default new Vuex.Store({
    state: {
        darkMode: false,
        currentMonth: {
            weeks: [],
            day1Index: 0,
            dayLastIndex: 0,
            index: 0,
            year: 0
        },
        months: [{name: 'January', days: 31}, {name: 'February', days: 28}, {name: 'March', days: 31}, {
            name: 'April',
            days: 30
        }, {name: 'May', days: 31}, {name: 'June', days: 30}, {name: 'July', days: 31}, {
            name: 'August',
            days: 31
        }, {name: 'September', days: 30}, {name: 'October', days: 31}, {name: 'November', days: 30}, {
            name: 'December',
            days: 31
        }],
        days: ['Sunday', 'Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday'],
        today: {},
        selectedLocation: {name: 'Select Location...'},
        calDates: {},
        isDateSelected: true,
        selectedDate: {},
        AppDate: {
            init(date, params) {
                this.dateText = date.year + '-' + ('0' + (date.month + 1)).slice(-2) + '-' + ('0' + date.date).slice(-2);

                if (params) {
                    if (params.calDates && params.calDates[this.dateText]) {
                        _.merge(this, params.calDates[this.dateText]);
                    } else {
                        _.merge(this, date);
                    }

                    this.today = params.todayText === this.dateText;
                } else {
                    _.merge(this, date);
                }
            },
            getDateText() {
                return this.year + '-' + ('0' + (this.month + 1)).slice(-2) + '-' + ('0' + this.date).slice(-2);
            },
            setColor(date, params) {
                if (this.today) {
                    this.bg = 'rgba(255, 165, 0, 0.7)';
                } else if (this.selected) {
                    this.bg = 'rgba(255, 165, 0, 0.4)';
                } else if (params && params.dark) {
                    this.bg = 'rgba(50, 50, 50, 0.6)';
                } else {
                    this.bg = 'rgba(255, 255, 255, 0.6)';
                }

                if (this.dayIndex === 0) {
                    this.color = 'rgb(255, 0, 0)';
                }
            }
        }
    },
    mutations: {
        setMonth(state, {date, dateIndex}) {
            let numOfDays = state.months[state.currentMonth.index].days,
                month = [],
                appDate;

            const AppDate = state.AppDate;

            state.currentMonth.week = [];
            state.currentMonth.day1Index = date === 1 ? dateIndex : ((dateIndex - (date % 7)) + 1 + 7) % 7;

            fillMonth(0, 1);

            function fillMonth(weekNo, date) {
                let lastWeek = false,
                    params = {
                        dark: state.dark,
                        todayText: state.today.dateText,
                        calDates: state.calDates[state.selectedLocation['city_id']]
                    },
                    itrDate;

                month.push([]);

                for (let i = 0; i < 7; i++) {
                    itrDate = {date, month: state.currentMonth.index, year: state.currentMonth.year, dayIndex: i};
                    appDate = Object.create(AppDate);
                    if ((weekNo == 0 && i < state.currentMonth.day1Index) || lastWeek) {
                        month[weekNo].push({});
                    } else {
                        appDate.init(itrDate, params);
                        month[weekNo].push(appDate);
                        if (date === numOfDays) {
                            lastWeek = true;
                            state.currentMonth.dayLastIndex = i;
                            state.currentMonth.weeks = month;
                        }
                        date++;
                    }
                }
                if (!lastWeek) {
                    fillMonth(weekNo + 1, date);
                }
            }
        },
        setDark(state, dark) {
            state.dark = dark;
        },
        setMonthVal(state, {key, val}) {
            state.currentMonth[key] = val;
        },
        setToday(state) {
            const today = new Date();
            let todayObj = Object.create(state.AppDate);
            todayObj.init({
                date: today.getDate(),
                day: today.getDay(),
                month: today.getMonth(),
                year: today.getFullYear()
            });
            state.today = todayObj;
        },
        setSelectedLocation(state, location) {
            state.selectedLocation = location;
            localStorage.location = location['city_id'];
        },
        setCalDates(state, locationImport) {
            _.each(locationImport, (date, dateText) => {
                let dateTextSplit = dateText.split('-'),
                    appDate = Object.create(state.AppDate);


                appDate.init({
                    dateText: dateText,
                    year: parseInt(dateTextSplit[0]),
                    month: parseInt(dateTextSplit[1]) - 1,
                    date: parseInt(dateTextSplit[2])
                });

                _.each(date.events, dateEvent => {
                    if (dateEvent.img) {
                        let url = 'url(' + require('../assets/img/' + dateEvent.img + '.jpg') + ')';
                        if (!date.url) {
                            date.url = url;
                        }
                        dateEvent.url = url;
                    }
                });
                _.merge(date, appDate);
            });

            state.calDates[state.selectedLocation['city_id']] = locationImport;
        },
        setIsDateSelected(state, isDateSelected) {
            state.isDateSelected = isDateSelected;
        },
        checkFeb(state) {
            if (state.currentMonth.index === 1 && (state.currentMonth.year % 4) === 0) {
                state.months[1].days = 29;
            } else {
                state.months[1].days = 28;
            }
        },
    },
    actions: {
        getPrev({commit, state}) {
            if (state.currentMonth.index === 0) {
                state.currentMonth.index = 11;
                state.currentMonth.year = state.currentMonth.year - 1;
                commit('checkFeb');
            } else {
                state.currentMonth.index = state.currentMonth.index - 1;
            }
            commit('setMonth', {
                date: state.months[state.currentMonth.index].days,
                dateIndex: (state.currentMonth.day1Index + 6) % 7
            });
        },
        getNext({commit, state}) {
            if (state.currentMonth.index === 11) {
                state.currentMonth.index = 0;
                state.currentMonth.year = state.currentMonth.year + 1;
                commit('checkFeb');
            } else {
                state.currentMonth.index = state.currentMonth.index + 1;
            }
            commit('setMonth', {date: 1, dateIndex: (state.currentMonth.dayLastIndex + 1) % 7});
        },
        selectDate({commit, state}, date) {
            let prevDate = state.selectedDate;
            if (date.dateText) {
                if (!date.day) date.day = new Date(date.dateText).getDay();

                if (date.month != state.currentMonth.index || date.year != state.currentMonth.year || state.currentMonth.weeks.length < 1) {
                    state.currentMonth.index = date.month;
                    state.currentMonth.year = date.year;
                    commit('checkFeb');
                    commit('setMonth', {date: date.date, dateIndex: date.day});
                }

                if (prevDate.dateText) {
                    prevDate.selected = false;
                }

                date.selected = true;
                state.selectedDate = date;
                state.isDateSelected = true;
            }
        },
        loadLocation({commit, state}, {location, locationImport}) {
            let dateToselect;

            commit('setSelectedLocation', location);

            if (!state.calDates[state.selectedLocation['city_id']]) {
                commit('setCalDates', locationImport);
            }

            if (state.selectedDate && state.selectedDate.date) {
                dateToselect = state.selectedDate;
            } else {
                dateToselect = state.today;
            }

            state.currentMonth.index = 0;

            return dateToselect;
        },
        selectLocation({state, dispatch}, {location, callback}) {
            if (!state.calDates[location['city_id']]) {
                import('../assets/cal-data/location-' + location['city_id'])
                    .then(locationImport => {
                        callback({location, locationImport: locationImport.default});
                    });
            } else {
                callback({location});
            }
        },
    },
    modules: {}
})
