<template>
    <div class="events">
        <div class="row events-options margin4 cross-center">
            <div class="cancel clickable w-15 h-100 row main-center cross-center" @click="toggleClose"
                 v-if="!isDateSelected">
                <i class="fa fa-close"></i>
            </div>
            <div class="w-15 clickable h-100 row cross-center main-center" @click="$store.dispatch('selectDate', today)"
                 title="Go to Today" v-else>
                <i class="fa fa-undo"></i>
            </div>
            <input type="text" v-model="searchText" id="search" class="flex search" placeholder="Search event"
                   @click="$store.commit('setIsDateSelected', false)">
            <div class="w-15 clickable h-100 row cross-center main-center"
                 @click="$store.commit('setIsDateSelected', false); events.filtered = calDates[selectedLocation['city_id']];"
                 title="View all events">
                <i class="fa fa-list"></i>
            </div>
        </div>
        <div class="list-container">
            <div v-if="isDateSelected && selectedDate.dateText">
                <DayEvents :day="selectedDate"/>
                <div v-if="selectedDate.dateText === today.dateText">
                    <div class="fw-bold upcoming row">Upcoming Events</div>
                    <div v-for="(day, dayIndex) in events.upcoming" :key="dayIndex">
                        <DayEvents :day="day"/>
                    </div>
                </div>
            </div>
            <div v-else>
                <div v-for="(day, dayIndex) in events.filtered" :key="dayIndex">
                    <DayEvents :day="day"/>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import _ from 'lodash';
import DayEvents from './DayEvents.vue';

export default {
    name: 'CalEventList',
    components: {
        DayEvents
    },
    computed: {
        today() {
            return this.$store.state.today;
        },
        isDateSelected() {
            return this.$store.state.isDateSelected;
        },
        selectedLocation() {
            return this.$store.state.selectedLocation;
        },
        months() {
            return this.$store.state.months;
        },
        calDates() {
            return this.$store.state.calDates;
        },
        filteredEvents() {
            return this.$store.state.filteredEvents;
        },
        selectedDate() {
            return this.$store.state.selectedDate;
        }
    },
    data() {
        return {
            searchText: '',
            events: {
                filtered: {},
                upcoming: [],
                appDate: {},
                setUpcoming(vm) {
                    this.appDate = Object.create(vm.$store.state.AppDate)
                    let keys = Object.keys(vm.calDates[vm.selectedLocation['city_id']]),
                        i = -1, count = 0;
                    this.appDate.init(vm.selectedDate);
                    while (count < 60) {
                        this.incrementDay(vm);
                        i = keys.indexOf(this.appDate.getDateText());
                        if (i > -1) break;
                        count++;
                    }
                    if (!(count >= 59 && i < 0)) {
                        while (i < keys.length && this.upcoming.length < 7) {
                            let day = vm.calDates[vm.selectedLocation['city_id']][keys[i]];
                            if (day) this.upcoming.push(day);
                            i++;
                        }
                    }
                },
                incrementDay(vm) {
                    if (this.appDate.date < vm.months[this.appDate.month].days) {
                        this.appDate.date++;
                    } else {
                        this.appDate.date = 1;
                        if (this.appDate.month < 11) {
                            this.appDate.month++
                        } else {
                            this.appDate.month = 0;
                            this.appDate.year++;
                        }
                    }
                },
                filter(vm) {
                    const keys = Object.keys(vm.calDates[vm.selectedLocation['city_id']]);
                    let i = 0, j = 0;
                    this.filtered = {};

                    _.each(vm.calDates[vm.selectedLocation['city_id']], (date, dateText) => {
                        _.each(date.events, dateEvent => {
                            if (dateEvent.name.toLowerCase().includes(vm.searchText.toLowerCase())) {
                                this.filtered[dateText] = date;
                            }
                        });
                    })
                }
            }
        }
    },
    created() {
        this.events.setUpcoming(this);
    },
    methods: {
        toggleClose() {
            let vm = this;
            vm.searchText = '';
            vm.$store.dispatch('selectDate', vm.selectedDate);
        }
    },
    watch: {
        searchText() {
            let vm = this;
            if (vm.searchText != '') {
                vm.events.filter(vm);
            }
        }
    }
}
</script>
<style lang="scss" scoped>
$all: ('', 0, 4000px);
$sm: ('-sm', 0, 767px);
$md: ('-md', 768px, 959px);
$lg: ('-lg', 960px, 4000px);

$breakpoints: ($all, $sm, $md, $lg);
@mixin sm {
    @media screen and (min-width: nth($sm, 2)) and (max-width: nth($sm, 3)) {
        @content;
    }
}

@mixin md {
    @media screen and (min-width: nth($md, 2)) and (max-width: nth($md, 3)) {
        @content;
    }
}

@mixin lg {
    @media screen and (min-width: nth($lg, 2)) and (max-width: nth($lg, 3)) {
        @content;
    }
}

.events {
    min-width: 200px;
    overflow: visible;

    .list {
        margin: 20px 0 10px 5px;
        border-radius: 3px;
        overflow: hidden;
    }

    .upcoming {
        font-size: 20px;
        margin-top: 50px;
        border-bottom: 1px solid grey;
    }
}

.list-container {
    @include lg {
        max-height: calc(100vh - 123px);
    }

    @include md {
        max-height: calc(100vh - 191px);
    }
}

// tablet + desktop
@media screen and (min-width: 768px) {
    .events {
        width: 35%;
    }

    .list-container {
        overflow-y: auto;
        padding-right: 5px;
    }

    .events-options {
        height: 35px;
    }

    .search {
        height: 29px;
    }
}

// mobile
@media screen and (max-width: 767px) {
    .events {
        width: 100%;
        margin-top: 10px;
    }

    .events-options {
        height: 45px;
    }

    .search {
        height: 40px;
        padding: 0 10px;
        border-radius: 5px;
    }
}
</style>