<template>
    <div class="home-container row-lg" :class="dark ? 'dark' : ''">
        <header class="row w-100 cross-center hide-lg shadow-10">
            <div class="menu-button clickable main-center cross-center hide-lg hide-xl row-sm row-md"
                 @click="menuDisplay = 'menu-display-flex'">
                &#9776;
            </div>
            <div class="flex row main-center cross-center">
                <h1>Sri Chaitanya Saraswat Math</h1>
            </div>
        </header>
        <div class="bars" :class="menuDisplay">
            <div class="sidebar">
                <div class="flex row main-center cross-center hide-md hide-sm">
                    <h1>Sri Chaitanya Saraswat Math</h1>
                </div>
                <div class="content">
                    <div class="row acharya main-space-around">
                        <Person
                            :person="{code: 'GovindaMhj', shortName: '<b>Sevaite Acharya:</b> Srila B.S. Govinda Maharaj'}"
                            :short="true"
                            @goTo="goTo('vaishnava/parampara/GovindaMhj')"
                        />
                        <Person
                            :person="{code: 'SridharMhj', shortName: '<b>Founder Acharya:</b> Srila B.R. Sridhar Maharaj'}"
                            :short="true"
                            @goTo="goTo('vaishnava/parampara/SridharMhj')"
                        />
                    </div>
                    <h3>Pages</h3>
                    <div class="column main-center">
                        <div class="row cross-center clickable menu-item"
                             @click="goTo('calendar')"
                             :class="{ active: $route.path === '/calendar' }">
                            Vaishnava Calendar
                        </div>
                        <div class="row cross-center clickable menu-item"
                             @click="goTo('books')"
                             :class="{ active: $route.path === '/books' }">
                            Books
                        </div>
                        <div class="row cross-center clickable menu-item"
                             @click="goTo('vaishnava/parampara')"
                             :class="{ active: $route.path.includes('/vaishnava/parampara') }">
                            Guru Parampara
                        </div>
                    </div>
                    <h3>
                        Dark mode
                        <label class="switch">
                            <input type="checkbox" v-model="dark" @click="toggleDarkMode"/>
                            <span class="slider round"></span>
                        </label>
                    </h3>
                </div>
            </div>
            <div class="rest" @click="menuDisplay = 'menu-display-none'"></div>
        </div>
        <router-view class="view flex"/>
    </div>
</template>
<script>
// @ is an alias to /src
import Person from "../components/Person.vue";

export default {
    name: "home",
    components: {Person},
    data() {
        return {
            showSidepanel: false,
            menuDisplay: "menu-display-none",
            widthLT960: false,
            dark: false,
            pages: [
                {
                    name: "SCS Math.com",
                    url: "http://scsmath.com",
                },
                {
                    name: "SCS Math International.com",
                    url: "http://scsmathinternational.com/index.php",
                },
                {
                    name: "Gaudiya Darshan.com",
                    url: "https://www.gaudiyadarshan.com/",
                },
                {
                    name: "Govinda Maharaj.com",
                    url: "https://govindamaharaj.com/",
                },
                {
                    name: "Hare Krishna.ru",
                    url: "https://harekrishna.ru/",
                },
            ],
            media: [
                {
                    name: "Bhakti Connect",
                    url: "https://www.youtube.com/c/BhaktiConnect",
                },
                {
                    name: "Infiniti Media",
                    url: "https://www.youtube.com/c/InfinitiMediaStream",
                },
            ],
        };
    },
    created() {
        var vm = this;
        vm.dark = JSON.parse(localStorage.dark);
        if (window.innerWidth < 860) {
            vm.widthLT960 = true;
        }
    },
    methods: {
        logOut() {
            localStorage.clear();
            location.reload();
        },
        goTo(name) {
            var vm = this;
            if (vm.$route.path != "/" + name) {
                vm.$router.push("/" + name);
            }
            vm.menuDisplay = "menu-display-none";
        },
        getImg(code) {
            var vm = this;
            let mobOpt = vm.widthLT960 ? "-mob" : "";
            return {
                "background-image": code
                    ? "url(" + require("../assets/img/" + code + mobOpt + ".jpg") + ")"
                    : "",
            };
        },
        toggleDarkMode() {
            localStorage.dark = JSON.stringify(!this.dark);
        },
        // setNotif() {
        // 	Notification.requestPermission().then((result) => {
        // 		if (result === 'granted') {
        // 			new Notification('Hello', { body: 'World' });
        // 		}
        // 	});
        // }
    },
};
</script>
<style lang="scss">
.dark {
    &.home-container,
    .menu-button,
    .view,
    .sidebar,
    a {
        background-color: rgb(50, 50, 50) !important;
        color: rgb(225, 225, 225);
    }
}

$all: ("", 0, 4000px);
$sm: ("-sm", 0, 767px);
$md: ("-md", 768px, 959px);
$lg: ("-lg", 960px, 4000px);

$breakpoints: ($all, $sm, $md, $lg);
@mixin sm {
    @media screen and (min-width: nth($sm, 2)) and (max-width: nth($sm, 3)) {
        @content;
    }
}

@mixin md {
    @media screen and (min-width: nth($md, 2)) and (max-width: nth($md, 3)) {
        @content;
    }
}

@mixin lg {
    @media screen and (min-width: nth($lg, 2)) and (max-width: nth($lg, 3)) {
        @content;
    }
}

.switch {
    position: relative;
    display: inline-block;
    width: 32px;
    height: 20px;
}

.switch input {
    opacity: 0;
    width: 0;
    height: 0;
}

.slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    -webkit-transition: 0.4s;
    transition: 0.4s;
}

.slider:before {
    position: absolute;
    content: "";
    height: 12px;
    width: 12px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    -webkit-transition: 0.4s;
    transition: 0.4s;
}

input:checked + .slider {
    background-color: #2196f3;
}

input:focus + .slider {
    box-shadow: 0 0 1px #2196f3;
}

input:checked + .slider:before {
    -webkit-transform: translateX(12px);
    -ms-transform: translateX(12px);
    transform: translateX(12px);
}

/* Rounded sliders */
.slider.round {
    border-radius: 34px;
}

.slider.round:before {
    border-radius: 50%;
}

.menu-button {
    border-radius: 3px;
    height: 50px;
    width: 50px;
    z-index: 0;
    background-color: white;
}

.menu-display-flex {
    display: flex;
}

h3 {
    padding-left: 5px;
    margin: 8px 0 4px;
}

header {
    h1 {
        margin: 5px 0;

        @include lg {
            font-size: 26px;
            word-break: keep-all;
            white-space: nowrap;
        }

        @include sm {
            font-size: 5.8vw;
            height: 50px;
            display: flex;
            align-items: center;
            justify-content: center;
        }
    }

    .president {
        font-size: 14px;
        margin-bottom: 5px;
    }

    .acharya1,
    .acharya2 {
        height: 80px;
        text-align: center;

        .img {
            margin: 0 10px;
            height: 80px;
            width: 80px;
            border-radius: 40px;
            background-size: 175%;
        }
    }

    .acharya1 > .img {
        background-position: center -60px;
        background-image: url(../assets/img/GovindaMhjHome.jpg);
    }

    .acharya2 > .img {
        background-position: center -10px;
        background-image: url(../assets/img/SridharMhjHome.jpg);
    }

    @include md {
        margin-bottom: 10px;
    }

    @include sm {
        margin-bottom: 10px;
    }
}

body {
    margin: 0;
}

// Desktop
@media screen and (min-width: 960px) {
    .view {
        // margin-left: 20vw;
        height: 100vh;
        overflow-y: auto;
        z-index: 3;
        // padding: 10px;
    }

    .sidebar {
        // top: 80px;
        // position: fixed;
        min-width: 25vw;
        width: 25vw;
        // height: calc(100vh - 50px);
        height: 100vh;
        background: white;
        box-shadow: 0 10px 12px rgba(0, 0, 0, 0.3);
        overflow-x: hidden;
        z-index: 2;

        .menu-item {
            padding: 5px 10px;
        }

        .content {
            max-height: 90vh;
            overflow: auto;
        }
    }

    .menu-display-none {
        display: flex;
    }

    h1 {
        font-size: 1.7vw;
    }
}

// Mobile + Tablet
@media screen and (max-width: 959px) {
    .home-container {
        padding: 8px;
    }

    .view {
        z-index: 0;
    }

    .bars {
        position: fixed;
        width: 100vw;
        height: 100vh;
        max-height: 100vh;
        z-index: 1;
        top: 0;
        left: 0;
        overflow-y: auto;
        flex-direction: column;
        background-color: rgba(0, 0, 0, 0.3);
    }

    .sidebar {
        left: 0;
        width: 75vw;
        height: 100vh;
        overflow: auto;
        background: white;

        .menu-item {
            padding: 5px 10px;
        }
    }

    .rest {
        position: fixed;
        min-height: 100vh;
        width: 25vw;
        top: 0;
        right: 0;
    }

    .menu-display-none {
        display: none;
    }
}
</style>