    <div>{{ cal }}</div>
  </div>
</template>
<script>
import _ from "lodash";

export default {
  name: "ListMgt",
  data() {
    return {
      cal: {}
    }
  },
  created() {
    let div = document.getElementById("web-cal"),
      month = "",
      year = "",
      date = "",
      lunarMonth = "",
      lunarDate = "",
      result = {},
      months = {
        january: "01",
        february: "02",
        march: "03",
        april: "04",
        may: "05",
        june: "06",
        july: "07",
        august: "08",
        september: "09",
        october: "10",
        november: "11",
        december: "12",
      },
      eventImages = {
        AcharyaMhj: "Nirmal Acharya",
        GovindaMhj: "Sundar Govinda",
        SwamiMhj: "Bhaktivedanta",
        SridharMhj: "Raksak Sridhar",
        SaraswatiMhj: "Siddhanta Saraswati",
        GauraKisoraMhj: "Paramahamsa Sri Srila Gaura Kishor",
        BhaktivinodaThakur: "Sachchidananda Bhakti Vinod",
        JagannathMhj: "Srila Jagannath Das Babaji",
        SriBalaram: "Sri Baladev",
        SriKrishna: "Janmashtami",
        SriRadha: "Radhashtami",
        SriGaura: "Gaura Purnima",
        SriNitai: "Sri Nityananda Prabhu",
      };

    _.each(div.childNodes, (node) => {
      // here
      if (node.localName === "p") {
        if (node.className && node.className === "month") {
          let splitMonth = node.firstChild.innerHTML.toLowerCase().split(" ");
          month = months[splitMonth[0]];
          year = splitMonth[1];
          if (year == "2022") year = "2023";
        } else if (node.getAttribute("align")) {
          lunarMonth = _.capitalize(node.firstChild.innerHTML);
        } else {
          let events = [],
            special = "",
            ekadashi = "";
          _.each(node.childNodes, (dayNode) => {
            let event = {},
              splitEvent = "";
            if (dayNode.className) {
              if (dayNode.className === "date") {
                ekadashi = "";
                special = "";
                date = ("0" + dayNode.innerHTML.split(" ")[0]).slice(-2);
              } else if (dayNode.className == "lunar-date") {
                lunarDate = dayNode.innerHTML.slice(0, -1);
              } else if (dayNode.className == "ekadashi") {
                ekadashi = dayNode.innerHTML;
              } else if (dayNode.className == "special") {
                special = dayNode.innerHTML;
              }
              if (event.name) {
                events.push(event);
              }
            } else {
              if (dayNode.innerHTML) {
                splitEvent = dayNode.innerHTML.split(". ");
              } else if (dayNode.textContent) {
                splitEvent = dayNode.textContent.split(". ");
              } else {
                splitEvent = dayNode.split(". ");
              }

              if (splitEvent.length) {
                _.each(splitEvent, (evName) => {
                  if (!_.isEmpty(_.trim(evName))) {
                    let ev = {
                      name: evName,
                    };

                    _.each(eventImages, (name, key) => {
                      if (ev.name.includes(name)) {
                        ev.img = key;
                      }
                    });

                    events.push(ev);
                  }
                });
              }
            }
          });

          if (events.length || ekadashi || special) {
            result[year + "-" + month + "-" + date] = {
              "lunar-day": lunarMonth + " " + lunarDate,
              ekadashi: ekadashi || undefined,
              special: special || undefined,
              events: events,
            };
          }
        }
      }
    });
    this.cal = result;
  },
};
</script>