<template>
    <div class="locationSelect shadow-10">
        <div class="row cross-center clickable h-100" @click="showLocationList = !showLocationList">
            <div class="flex">{{ selectedLocation.name }}</div>
            <div class="w-10"><i class="fa fa-caret-down"></i></div>
        </div>
        <div class="locationList" v-if="showLocationList">
            <div v-for="location in locationList" :key="location['city_id']">
                <div class="clickable" @click="selectLocation(location)">{{ location.name }}</div>
            </div>
        </div>
    </div>
</template>
<script>
import cities from '../assets/js/cities';

export default {
    name: 'CalLocationSelect',
    computed: {
        selectedLocation() {
            return this.$store.state.selectedLocation;
        }
    },
    data() {
        return {
            showLocationList: false,
            locationList: cities
        }
    },
    created() {
        const vm = this;

        const location = localStorage.location;

        _.each(cities, city => {
            if (city['city_id'] == location) {
                vm.$store.dispatch('selectLocation', {location: city, callback: vm.loadLocation});
                return false;
            }
        });
    },
    methods: {
        loadLocation(locationObj) {
            const vm = this;
            vm.$store.dispatch('loadLocation', locationObj)
                .then(dateToselect => {
                    vm.$store.dispatch('selectDate', dateToselect);
                });
        },
        selectLocation(location) {
            let vm = this;

            vm.$store.dispatch('selectLocation', {location, callback: vm.loadLocation});
            vm.showLocationList = false;
        }
    }
}
</script>
<style lang="scss" scoped>
.locationSelect {
    border-radius: 3px;
    margin-bottom: 20px;
    height: 50px;
}

.locationSelect {
    & > div:nth-of-type(1) {
        padding-left: 10px;
    }
}

.locationList {
    position: absolute;
    background-color: white;
    box-shadow: 0px 0px 12px rgba(0, 0, 0, 0.3);
    min-width: 200px;
    overflow-y: auto;
    z-index: 2;
    max-height: 500px;

    .clickable {
        padding: 10px;
    }
}

@media screen and (min-width: 768px) {
    .locationSelect {
        width: 35%;
    }

    .locationSelect {
        min-width: 200px;
        margin-left: 10px;
    }
}

@media screen and (min-width: 960px) {
    .locationList {
        width: 25.5vw;
    }
}

@media screen and (min-width: 768px) and (max-width: 959px) {
    .locationList {
        width: calc(100vw - 51px);
    }
}

@media screen and (max-width: 767px) {
    .locationList {
        width: calc(100vw - 51px);
    }
}
</style>