<template>
    <div class="col month">
        <div class="row">
            <div class="row flex main-center day" v-for="(day, dayIndex) in days"
                 :class="{ 'fc-dark-red' : dayIndex === 0 }" :key="dayIndex">{{ day.substring(0, 3) }}
            </div>
        </div>
        <div class="row flex" v-for="(week, weekIndex) in currentMonth.weeks" :key="weekIndex">
            <div v-for="(date, dateIndex) in week" :key="dateIndex"
                 :class="{ selectedDate: selectedDate.dateText === date.dateText, today: today.dateText === date.dateText, 'fc-dark-red' : dateIndex === 0 }"
                 class="flex row main-center cross-center date clickable" :style="styleObject(date)"
                 @click="$store.dispatch('selectDate', date)">
                {{ date.date }}
                <div class="dot red row main-center cross-center" v-if="date && date.ekadashi"></div>
                <div class="dot blue row main-center cross-center"
                     v-if="date && date.events && ((date.events.length > 0) || (date.special))">
                    {{ date.special ? date.events.length + 1 : date.events.length }}
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    name: 'CalMonth',
    computed: {
        today() {
            return this.$store.state.today;
        },
        selectedLocation() {
            return this.$store.state.selectedLocation;
        },
        months() {
            return this.$store.state.months;
        },
        calDates() {
            return this.$store.state.calDates;
        },
        month() {
            return this.$store.state.month;
        },
        currentMonth() {
            return this.$store.state.currentMonth;
        },
        days() {
            return this.$store.state.days;
        },
        selectedDate() {
            return this.$store.state.selectedDate;
        }
    },
    methods: {
        styleObject(date) {
            let vm = this;
            let mobOpt = vm.$parent.$parent.widthLT960 ? '-mob' : '';
            return {
                '--eventBgImg': date && date.img ? 'url(' + require('../assets/img/' + date.img + mobOpt + '.jpg') + ')' : ''
            }
        },
    }
}
</script>
<style lang="scss" scoped>
span {
    margin: 10px 5px;
    padding: 5px;
    border-radius: 3px;
    background-image: linear-gradient(rgba(250, 88, 88, 0.3), rgba(250, 88, 88, 0.5));
}

.dark .date {
    background-color: rgba(50, 50, 50, 0.6);
}

.month {
    z-index: 0;
}

.date {
    border-radius: 3px;
    position: relative;
    background-color: rgba(255, 255, 255, 0.6);

    &::after {
        content: " ";
        position: absolute;
        z-index: -1;
        border-radius: 3px;
        background-image: var(--eventBgImg);
        background-repeat: no-repeat;
        background-size: cover;
        background-position-x: center;
        width: 100%;
        height: 100%;
    }
}

.day {
    margin-bottom: 15px;
}

.today,
.dark .today {
    background-color: rgba(255, 165, 0, 0.7);

    &:hover {
        background-color: rgba(255, 165, 0, 0.8);
    }
}

.selectedDate,
.dark .selectedDate {
    &:not(.today) {
        background-color: rgba(255, 165, 0, 0.4);
    }

    .selectedDate:not(.today):hover {
        background-color: rgba(255, 165, 0, 0.5);
    }
}

.dot {
    position: absolute;
    width: 15px;
    height: 15px;
    border-radius: 7.5px;
    margin-top: 30px;

    &.red {
        background-color: #FA5858;
        margin-left: -30px;
    }

    &.blue {
        background-color: goldenrod;
        margin-left: 30px;
        font-size: 12px;
        font-weight: bold;
        color: white;
    }
}

.fc-dark-red {
    // color: darkred;
    color: red;
}

@media screen and (min-width: 768px) {
    .month {
        width: 65%;
        padding: 0 20px;
        box-sizing: border-box;
    }
}

@media screen and (max-width: 767px) {
    .month {
        width: 100%;
    }

    .date {
        min-height: 60px;
    }
}
</style>