<template>
    <div class="list shadow-10">
        <div class="title row main-left cross-center padding8" @click="$store.dispatch('selectDate', day)">
            <div class="flex fw-bold">
                <span>{{ day.date }} {{ months[day.month].name }} {{ day.year }}</span>
                <span v-if="day.dateText === today.dateText"> - Today</span>
                <span v-if="day['lunar-day']"> - {{ day['lunar-day'] }}</span>
            </div>
        </div>
        <div>
            <div class="event clickable not special" v-if="day.special">{{ day.special.name }}</div>
            <div class="event clickable not special11" v-if="day.ekadashi">{{ day.ekadashi.name }}</div>
            <a v-for="(event, eventIndex) in day.events" :key="eventIndex" :href="event.link" target="_blank"
               class="event clickable row cross-center" :class="{ not: !event.link, 'fw-bold': event.link }">
                <div v-if="event.img" class="event-image"
                     :style="{ 'background-image': 'url(' + require('../assets/img/' + event.img + '.jpg') + ')' }"></div>
                <div>{{ event.name }}</div>
            </a>
        </div>
    </div>
</template>
<script>
export default {
    name: 'DayEvents',
    props: ['day'],
    computed: {
        today() {
            return this.$store.state.today;
        },
        view() {
            return this.$store.state.view;
        },
        selectedLocation() {
            return this.$store.state.selectedLocation;
        },
        months() {
            return this.$store.state.months;
        },
        calDates() {
            return this.$store.state.calDates;
        },
        filteredEvents() {
            return this.$store.state.filteredEvents;
        },
        selectedDate() {
            return this.$store.state.selectedDate;
        }
    }
}
</script>
<style lang="scss" scoped>
.dark {
    .title {
        background-image: linear-gradient(rgba(255, 165, 0, 0.4), rgba(255, 165, 0, 0.2));
    }

    .special {
        background-color: rgba(218, 165, 32, 0.5);
    }

    .special11 {
        background-color: rgba(250, 88, 88, 0.5);
    }
}

.title {
    background-image: linear-gradient(rgba(255, 165, 0, 0.4), rgba(255, 165, 0, 0.8));
    cursor: pointer;
}

.special {
    background-color: rgba(218, 165, 32, 0.3);
}

.special11 {
    background-color: rgba(250, 88, 88, 0.3);
}


.event {
    text-align: left;
    padding: 10px;

    .event-image {
        min-width: 20px;
        max-width: 20px;
        height: 20px;
        border: 1px solid grey;
        border-radius: 10px;
        margin-right: 8px;
        background-repeat: no-repeat;
        background-size: cover;
        background-position-x: center;
    }
}
</style>