<template>
    <div class="main">
        <div class="header">
            <div class="monthSelect row flex-md flex-lg shadow-10"
                 v-if="selectedLocation.name !== 'Select Location...'">
                <div class="row main-center cross-center clickable flex-md flex-lg" @click="$store.dispatch('getPrev')">
                    <i class="fa fa-caret-left"></i>
                </div>
                <div class="row main-center cross-center clickable flex">{{ months[currentMonth.index].name }}
                    {{ currentMonth.year }}
                </div>
                <div class="row main-center cross-center clickable flex-md flex-lg" @click="$store.dispatch('getNext')">
                    <i class="fa fa-caret-right"></i>
                </div>
            </div>
            <CalLocationSelect/>
        </div>
        <div class="container" v-if="selectedLocation.name !== 'Select Location...'">
            <CalMonth/>
            <CalEventList v-if="selectedDate.dateText"/>
        </div>
    </div>
</template>
<script>
import CalEventList from '../components/CalEventList.vue';
import CalLocationSelect from '../components/CalLocationSelect.vue';
import CalMonth from '../components/CalMonth.vue';

export default {
    name: 'Calendar',
    components: {
        CalEventList,
        CalLocationSelect,
        CalMonth
    },
    computed: {
        selectedLocation() {
            return this.$store.state.selectedLocation;
        },
        selectedDate() {
            return this.$store.state.selectedDate;
        },
        months() {
            return this.$store.state.months;
        },
        currentMonth() {
            return this.$store.state.currentMonth;
        }
    },
    created() {
        let vm = this;
        vm.$store.commit('setToday');
    }
}
</script>
<style lang="scss" scoped>
.main {
    padding: 10px;
}

.header {
    display: flex;
}

.monthSelect {
    border-radius: 3px;
    margin-bottom: 20px;
    height: 50px;
}

.container {
    display: flex;
    flex-direction: row;
}

//Desktop + Tablet
@media screen and (min-width: 768px) {
    .header {
        flex-direction: row;
    }

    .month {
        width: 65%;
        padding: 0 20px;
        box-sizing: border-box;
    }

    .container {
        min-height: calc(100vh - 166px);
        max-height: calc(100vh - 166px);
    }
}

//Mobile
@media screen and (max-width: 767px) {
    .header {
        flex-direction: column-reverse;
    }

    .monthSelect > div:nth-child(odd) {
        width: 20%;
        flex: none;
    }

    .container {
        display: flex;
        flex-direction: column;
    }
}
</style>