<template>
    <div :class="short ? 'short' : 'space'" @click="getTo()" class="clickable person shadow-15">
        <div class="img"
             :style="{ 'background-image': 'url(' + require('../assets/img/' + person.code + '.jpg') + ')' }"
             v-if="!person.boxText"></div>
        <div class="boxText row main-center cross-center" v-else>{{ person.boxText }}</div>
        <div class="label" v-html="person.shortName"></div>
    </div>
</template>
<script>
export default {
    name: 'Person',
    props: {
        person: Object,
        short: true
    },
    methods: {
        getTo() {
            const vm = this;
            let url;
            if (!vm.short) {
                if (vm.person.url) {
                    url = vm.person.url;
                } else {
                    url = `${vm.$route.params.category}/${vm.person.code}`;
                }
                vm.$router.push(url);
            } else {
                vm.$emit('goTo');
            }
        }
    }
}
</script>
<style lang="scss" scoped>
.person {
    border-radius: 5px;

    .img {
        overflow: hidden;
        border-radius: 5px;
        background-repeat: no-repeat;
        background-size: cover;
        background-position-x: center;
    }

    .boxText {
        font-size: 24px;
        height: 250px;
        width: 150px;
        overflow: hidden;
        text-align: center;
        margin: 10px;
    }
}

.short {
    height: 260px;
    max-width: 130px;
    margin: 10px 0;

    .img {
        height: 200px;
        width: 130px;
    }

    .label {
        padding: 5px;
        font-size: 14px;
    }
}

.space {
    height: 310px;
    max-width: 170px;
    margin: 10px;

    .img {
        height: 250px;
        width: 150px;
        margin: 10px 10px 0;
    }

    .label {
        padding: 5px 15px;
    }
}
</style>