<template>
    <div class="container">
        <div v-for="section in sections" :key="section.name">
            <div v-if="section.books && section.books.length > 0">
                <div class="margin8 guru">{{ section.name }}</div>
                <div class="row wrap">
                    <div v-for="book in section.books" class="book col margin8" :key="book.name">
                        <div class="col flex clickable w-100 cross-center" @click="$router.push('books/' + book.code)">
                            <img :src="require('../assets/img/book-cover/' + book.code + '.jpg')" height="125"
                                 class="margin8">
                            <div class="row main-center text-center margin8 name">{{ book.name }}</div>
                        </div>
                        <a :href="require('../assets/pdf/' + book.code + '.pdf')" target="_blank"
                           class="padding8 row main-center clickable">Download</a>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import gurus from '../assets/js/parampara';

export default {
    name: 'Books',
    data() {
        return {
            sections: [],
            selectedBook: false
        }
    },
    created() {
        var vm = this;
        vm.sections.push(gurus[1]);
        vm.sections.push(gurus[0]);
        vm.sections.push({
            name: 'Useful Books',
            books: [{
                name: 'Kirttan Guide - 8th Edition',
                code: 'KirttanGuide'
            }, {
                name: 'Daily Service Life Guide',
                code: 'ServiceLife'
            }]
        })
    }
}
</script>
<style lang="scss" scoped>
.container {
    padding: 10px;
}

.guru {
    font-size: 25px;
    font-weight: bold;
}

.book {
    max-width: 130px;

    .name {
        font-weight: bold;
    }

    img {
        border-radius: 3px;
        box-shadow: 0 0 8px rgba(0, 0, 0, 0.5);
    }
}
</style>